<template><div class="markdown-body"><h1>弹出框</h1>
<p><code v-pre="">mu-popover</code> 用于弹出显示，它封装在 <code v-pre="">mu-menu</code> 组件内部。</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-button ref=\&quot;button\&quot; @click=\&quot;open = !open\&quot;&gt;Dropdown&lt;/mu-button&gt;\n&lt;mu-popover cover :open.sync=\&quot;open\&quot; :trigger=\&quot;trigger\&quot;&gt;\n   &lt;mu-list&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Refresh&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Send feedback&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Settings&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Help&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n    &lt;mu-list-item button&gt;\n      &lt;mu-list-item-title&gt;Sign out&lt;/mu-list-item-title&gt;\n    &lt;/mu-list-item&gt;\n  &lt;/mu-list&gt;\n&lt;/mu-popover&gt;\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      open: false,\n      trigger: null\n    }\n  },\n  mounted () {\n    this.trigger = this.$refs.button.$el;\n  }\n}\n&quot;,&quot;style&quot;:null}">
                    <div class="source" slot="source"><mu-button ref="button" @click="open = !open">Dropdown</mu-button>
<mu-popover cover="" :open.sync="open" :trigger="trigger">
   <mu-list>
    <mu-list-item button="">
      <mu-list-item-title>Refresh</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Send feedback</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Settings</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Help</mu-list-item-title>
    </mu-list-item>
    <mu-list-item button="">
      <mu-list-item-title>Sign out</mu-list-item-title>
    </mu-list-item>
  </mu-list>
</mu-popover>

</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">ref</span>=<span class="hljs-string">"button"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"open = !open"</span>&gt;</span>Dropdown<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">mu-popover</span> <span class="hljs-attr">cover</span> <span class="hljs-attr">:open.sync</span>=<span class="hljs-string">"open"</span> <span class="hljs-attr">:trigger</span>=<span class="hljs-string">"trigger"</span>&gt;</span>
   <span class="hljs-tag">&lt;<span class="hljs-name">mu-list</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Refresh<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Send feedback<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Settings<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Help<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item</span> <span class="hljs-attr">button</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-list-item-title</span>&gt;</span>Sign out<span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item-title</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list-item</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-list</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">mu-popover</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
<span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
  data () {
    <span class="hljs-keyword">return</span> {
      <span class="hljs-attr">open</span>: <span class="hljs-literal">false</span>,
      <span class="hljs-attr">trigger</span>: <span class="hljs-literal">null</span>
    }
  },
  mounted () {
    <span class="hljs-keyword">this</span>.trigger = <span class="hljs-keyword">this</span>.$refs.button.$el;
  }
}
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="popover-props"><a class="header-anchor" href="#popover-props" aria-hidden="true">¶</a> Popover Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>cover</td>
<td>弹出是否覆盖触发元素</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>lazy</td>
<td>是否在打开时才渲染元素</td>
<td>Boolean</td>
<td>—</td>
<td>—</td>
</tr>
<tr>
<td>placement</td>
<td>弹出位置</td>
<td>String</td>
<td>top / top-start / top-end / bottom / bottom-start / bottom-end / left / left-start / left-end / right / right-start / right-end</td>
<td>bottom-start</td>
</tr>
<tr>
<td>open</td>
<td>是否打开</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>trigger</td>
<td>触发元素</td>
<td>Element</td>
<td>—</td>
<td>—</td>
</tr>
</tbody>
</table>

</div class="markdown-body"></template>

<script>
export default {
  data () {
    return {
      open: false,
      trigger: null
    }
  },
  mounted () {
    this.trigger = this.$refs.button.$el;
  }
}
</script>


